button,
.button {
    padding: 13px 30px;
    background: $purple;
    border: none;
    color: $white;
    margin: 15px 0;
    font-size: 16px;
    display: inline-block;
    text-decoration: none;
    font-weight: 600;
    font-family: 'Open Sans';
    cursor: pointer;
    text-align: center;
    position: relative;
    z-index: 1;
    color: #fff;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;

    -webkit-transition: box-shadow 0.2s;

    transition: box-shadow 0.2s;

    background: $purple;
}
button:hover, .button:hover {
    box-shadow: 0 0 0 4px $purple,
            inset 0 0 0 3px $white;
    color: #fff;
}
button.reversed, .button.reversed {
    -webkit-transition: box-shadow 0.2s;
    transition: box-shadow 0.2s;
    background: $white;
    color: $purple;
}
button.reversed:hover, .button.reversed:hover {
    box-shadow: 0 0 0 4px $white,
            inset 0 0 0 3px $purple;
    color: $purple;
}
button.blue, .button.blue {
    background: $blue;
    -webkit-transition: box-shadow 0.2s;
    transition: box-shadow 0.2s;
    background: $blue;
}
button.blue:hover, .button.blue:hover {
    box-shadow: 0 0 0 4px $blue,
            inset 0 0 0 3px $white;
    color: #fff;
}
button.blue.reversed, .button.blue.reversed {
    -webkit-transition: box-shadow 0.2s;
    transition: box-shadow 0.2s;
    background: $white;
    color: $blue;
}
button.blue.reversed:hover, .button.blue.reversed:hover {
    box-shadow: 0 0 0 4px $white,
            inset 0 0 0 3px $blue;
    color: $blue;
}
button.gold, .button.gold {
    background: $gold;
    -webkit-transition: box-shadow 0.2s;
    transition: box-shadow 0.2s;
    background: $gold;
}
button.gold:hover, .button.gold:hover {
    box-shadow: 0 0 0 4px $gold,
            inset 0 0 0 3px $white;
    color: #fff;
}
button.green, .button.green {
    background: $green;
    -webkit-transition: box-shadow 0.2s;
    transition: box-shadow 0.2s;
    background: $green;
}
button.green:hover, .button.green:hover {
    box-shadow: 0 0 0 4px $green,
            inset 0 0 0 3px $white;
    color: #fff;
}
button.light-green, .button.light-green {
    background: $green_light;
    -webkit-transition: box-shadow 0.2s;
    transition: box-shadow 0.2s;
    background: $green_light;
}
button.light-green:hover, .button.light-green:hover {
    box-shadow: 0 0 0 4px $green_light,
            inset 0 0 0 3px $white;
    color: #fff;
}
